"use client";

import { useEffect, useState } from "react";
import Quagga from "quagga";
import { getDatabase, ref, set } from "firebase/database";
import { initializeApp } from "firebase/app";
import { firebaseConfig } from "../firebase/firebaseConfig";
 // Adjust the import path if needed

// Initialize Firebase and get a reference to the Realtime Database.
initializeApp(firebaseConfig);
const db = getDatabase();

export default function ScanPage() {
  const [barcode, setBarcode] = useState(null);
  const [productName, setProductName] = useState("");
  const [scanned, setScanned] = useState(false);

  useEffect(() => {
    if (!scanned) {
      Quagga.init(
        {
          inputStream: {
            type: "LiveStream",
            constraints: {
              width: 640,
              height: 480,
              facingMode: "environment",
            },
          },
          decoder: { readers: ["ean_reader", "upc_reader"] },
        },
        (err) => {
          if (err) {
            console.error("Error initializing scanner:", err);
            return;
          }
          Quagga.start();
        }
      );

      Quagga.onDetected((data) => {
        if (!scanned) {
          setBarcode(data.codeResult.code);
          setScanned(true);
          Quagga.stop();
        }
      });
    }
  }, [scanned]);

  const handleConfirm = async () => {
    if (!barcode || !productName) {
      alert("Both barcode and product name are required!");
      return;
    }
    try {
      const productRef = ref(db, `inventory/${barcode}`);
      console.log("Saving product:", { barcode, name: productName });
      await set(productRef, { barcode, name: productName });
      alert(`Product "${productName}" saved successfully!`);
      setBarcode(null);
      setProductName("");
      setScanned(false);
      Quagga.start();
    } catch (error) {
      console.error("Error saving product:", error);
      alert("Failed to save product.");
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4 bg-gray-50">
      <h1 className="text-3xl font-bold mb-4">Scan Product</h1>
      {barcode ? (
        <div>
          <p>Scanned Barcode: {barcode}</p>
          <input
            type="text"
            placeholder="Enter Product Name"
            value={productName}
            onChange={(e) => setProductName(e.target.value)}
            className="p-2 border rounded"
          />
          <button
            onClick={handleConfirm}
            className="mt-2 bg-blue-500 text-white py-2 px-4 rounded"
          >
            Confirm
          </button>
        </div>
      ) : (
        <p>No barcode detected yet.</p>
      )}
    </div>
  );
}
